<template>
	<view>
		<navbar Navtitle='公开模型' :leftIconBool='false' :navBoolean='false' @back='back' @notice='notice'></navbar>
		<view class="" style="padding: 8rpx 30rpx;background-color: #FFFFFF;">
			<u-search @search='search' @custom='search' placeholder="模型相关信息" v-model="searchContent" shape="round" bg-color='#ffffff' border-color='#EEEEEE' height='64' :clearabled="true" :show-action="true" action-text="搜索" :animation="true"></u-search>
		</view>
		<view class="sections" style="">
			<classifyModel :publicData='publicData'></classifyModel>
			<u-empty class="empty" text="暂无更多数据" mode="list" v-if="show"></u-empty>
		</view>
		<u-no-network></u-no-network>
	</view>
</template>

<script>
	import classifyModel from '@/components/model/classifyModel.vue'
	import {getPublicModel} from '@/utill/api/model/getModel.js'
	export default {
		components:{
			classifyModel
		},
		data() {
			return {
				publicData:[],
				searchContent:'',
				show:false
			}
		},
		methods: {
			back(){
				uni.navigateBack({
					delta:1
				})
			},
			notice(){
				console.log('notice')
			},
			search(){
				if(!this.searchContent){
					uni.showToast({
						title:'搜索内容不能为空',
						icon:'none'
					})
					return
				}
				getPublicModel({
					modelName:this.searchContent
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						uni.hideLoading()
						if(res.data.object.list.length==0){
							uni.showToast({
								title:'数据为空',
								icon:'none'
							})
						}else{
							this.publicData=res.data.object.list
						}
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					uni.hideLoading()
					uni.showToast({
						title:'err',
						icon:'none'
					})
					console.log(err)
				})
			},
			getPublicModel(){
				var timer = setTimeout(function() {
					uni.showLoading({
					  title: '数据加载中...',
					})
				}, 2000)
				getPublicModel({}).then(res=>{
					console.log(res)
					clearTimeout(timer)
					uni.hideLoading()
					if(res.data.code==0){
						this.publicData=res.data.object.list
						if(res.data.object.list.length==0){
							this.show=true
						}else{
							this.show=false
						}
					}
				}).catch(err=>{
					clearTimeout(timer)
					uni.hideLoading()
					uni.showToast({
						title:'err',
						icon:'none'
					})
					console.log(err)
				})
			}
		},
		onLoad() {
			this.getPublicModel()
		}
	}
</script>

<style scoped lang="scss">
	.header_top{
		background-color: #FFFFFF;
		width: 100%;
		z-index: 99999;
	}
	.header_top /deep/ .uni-searchbar__box{ 
		justify-content: flex-start!important;
	}
	.header_top /deep/ .uni-searchbar{
		padding-left: 30rpx;
		padding-right: 30rpx;
	}
	.sections{
		padding: 0 30rpx;
		margin-top: 20rpx;
		flex: 1;
		/* #ifdef H5 */
			height: calc(100vh - 88rpx - 104rpx);
		/* #endif */
		/* #ifdef APP-PLUS */
			height: calc(100vh - 88rpx - 104rpx - 88rpx);
		/* #endif */
		overflow-y: auto;
		overflow-x: hidden;
		-webkit-overflow-scrolling: touch;
	}
</style>
